<template>
  <div class="login-container">
    <div class="m-c">
      <div class="login-box">
        <img src="~@/assets/images/login-title.png" style="width:350px;margin-left:11px;margin-bottom:26px">

        <div class="box-container">
          <Form ref="formValidate" :model="formValidate" :rules="ruleValidate">
            <FormItem prop="phone_num">
              <Input size="large" v-model="formValidate.phone_num" placeholder="用户名/手机号">
              <img src="~@/assets/images/login-user.png" slot="prefix" style="width:19px;">
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input type="password" size="large" v-model="formValidate.password" placeholder="密码">
              <img src="~@/assets/images/password-icon.png" slot="prefix" style="width:19px;">
              </Input>
            </FormItem>
            <FormItem prop="vcode">
              <div class="flex-ac">
                <Input size="large" v-model="formValidate.vcode" style="width:180px">
                </Input>
                <div class="verification-img">
                  <validate ref="validate" v-bind:value.sync="validateCode"></validate>
                </div>
                <a @click="$refs.validate.refreshCode()"> 换一组 </a>
              </div>
            </FormItem>
            <Button type="primary" class="login-btn" @click.native="login">登 录</Button>
          </Form>
          <div class="flex-spb mt-19">
            <!-- <Checkbox v-model="autoLogin">下次自动登录</CheckBox> -->
            <span></span>
            <router-link style="color:#999999" to="/forget">忘记密码？</router-link>
          </div>
          <p class="mt-20" style="text-align:center">
            <span class="c-gray">还未注册?</span>
            <router-link to="/register"> 注册
            </router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import validate from './validate'
import md5 from 'js-md5'
import localStore from '@/utils/localstore.js'
export default {
  components: {
    validate
  },
  data () {
    const validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写手机号码'));
      } else if (/^1[3456789]\d{9}$/.test(value)) {
        callback();
      } else {
        callback(new Error('请正确填写手机号码'));
      }
    };
    const valitateVcode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写验证码'));
      } else if (this.validateCode.toLocaleLowerCase() != this.formValidate.vcode.toLocaleLowerCase()) {
        callback(new Error('验证码不正确'));
      } else {
        callback();
      }
    };
    return {
      autoLogin: false,
      validateCode: '',
      formValidate: {
        phone_num: '',
        password: '',
        vcode: ''
      },
      ruleValidate: {
        phone_num: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '请输入6-16位密码', trigger: 'blur' },
        ],
        vcode: [{ validator: valitateVcode, trigger: 'blur' }]
      }
    }
  },
  methods: {
    login () {
      let self = this
      this.$refs['formValidate'].validate(async (valid) => {
        if (valid) {
          let params = {
            phone_num: self.formValidate.phone_num,
            passwd: md5(self.formValidate.password)
          }
          let resData = await this.$post('cgi/signin', params);
          if (resData.ret_code >= 0) {
            localStore.set('self_id', resData.ret_data.mch_id);
            localStore.set('sess', resData.ret_data.sess, false);
            self.$store.dispatch('user/submitTel', params.phone_num)
            self.$router.push({
              path: self.$route.query.redirect ? self.$route.query.redirect : '/home'
            })
          }
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.login-container {
  background: url(~@/assets/images/bj.png) no-repeat center center fixed;
  background-size: cover;
  .login-btn {
    width: 100%;
    height: 48px;
    font-size: 20px;
  }
  .m-c {
    max-width: 1200px;
    position: relative;
    margin: auto;
    height: 100vh;
    .login-box {
      position: absolute;
      right: 50px;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .box-container {
        width: 400px;
        height: 473px;
        border-radius: 10px;
        background-color: #fff;
        padding: 33px 20px;
      }
      .verification-img {
        width: 96px;
        height: 39px;
        border-radius: 5px;
        background-color: #98c1f8;
        margin-left: 22px;
        margin-right: 10px;
      }
    }
  }
}
/deep/ .ivu-input-large {
  height: 48px;
}
/deep/ .ivu-input {
  background-color: #eeeeee;
  border: none;
}
/deep/ .ivu-input-with-prefix {
  padding-left: 41px;
}
/deep/ .ivu-input-prefix,
/deep/ .ivu-input-suffix {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>